@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;700&display=swap);
* {
  margin: 0;
  padding: 0;
}


.navigation {
  background-color: black;
}
.main_logo {
  width: 60px;
}

.menu {
  display: flex;
  list-style: none;
  height: 100%;
  align-items: center;
  text-align: center;
}

.menu .menu_item {
  flex-basis: 50%;
  padding: 10px;
  cursor: pointer;
  color: white;
}

.menu .menu_item:hover {
  background-color: red;
}

/* Nav styling */

.black_nav {
  background-color: black !important;
}

.responsive-navbar-nav {
  background-color: brown;
}

.nav_element {
  color: white;
  padding: 10px;
}
.nav_element:hover {
  background: #dc3545;
  color: white;
  text-decoration: none;
}

.footer {
    background-color: white;
}
#bgc_container {
  background-image: url(/static/media/main.bc1233ab.jpg);
  background-size: cover;
  background-position: top;
  min-height: 60vh;
  position: relative;
}

.custom_btn {
  border-radius: 29px;
  padding-left: 4rem;
  padding-right: 4rem;
  margin-top: 10vh;
  position: relative;
  z-index: 100;
}

.custom_desc {
  font-size: 1.5rem;
}

.cover {
  position: absolute;
  background-image: url(/static/media/cover.5e265958.svg);
  bottom: -1px;
  height: 2.5vw;
  z-index: 10;
  width: 100%;
  background-size: 100% 2.5vw;
}



@media (max-width: 500px) {
  .main_heading {
    font-size: 3.5rem;
  }
  #bgc_container {
    min-height: 90vh;
  }
}

@media (orientation: landscape) {
  .custom_btn {
    margin-top: 5vh;
  
  }
  
}

.about {
    background-color: white;
}

.custom_h2 {
    font-size: 3.5rem;
}

@media (max-width: 500px) {
    .about {
        text-align: center;
        padding: 25px;
    }
    .custom_h2 {
        font-size: 3rem;
    }
  }
